<template>
  <div class="menBox">
    <div class="initArea">
      <h2>お尋ね『<span :class="answer">者</span>』を探せ！</h2>
    </div>
    <p
      v-for="(man, index) in men"
      :key="index"
      :class="man"
      @click="checkingAnswers(man)"
    >
      者
    </p>
    <div class="resultDialog" :class="result == null ? 'none' : ''">
      <div v-if="result == true" class="answerArea">
        <h2>正解！！！</h2>
        <div class="emoticon">(*´ω｀*)</div>
        <div @click="close" class="close">閉じる</div>
      </div>
      <div v-else class="answerArea">
        <h2>不正解！！</h2>
        <div class="emoticon">(´；ω；｀)</div>
        <div @click="close" class="close">閉じる</div>
      </div>
      <div @click="close" class="overlay"></div>
    </div>
    <div class="loading" :class="isLoading ? '' : 'none'">
      <div class="container">
        <p class="circle-text">
          <span>お</span><span>尋</span><span>ね</span><span>『</span
          ><span>者</span><span>』</span><span>を</span><span>探</span
          ><span>せ</span><span>！</span><span>！</span><span>！</span>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "WantedMan",
  data() {
    return {
      men: [
        "delaGothicOne",
        "dotGothic16",
        "hachiMaruPop",
        "hinaMincho",
        "kaiseiDecol",
        "kaiseiTokumin",
        "kiwiMaru",
        "kleeOne",
        "kosugiMaru",
        "mPLUS1p",
        "newTegomin",
        "notoSansJP",
        "pottaOne",
        "rampartOne",
        "reggaeOne",
        "rocknRollOne",
        "sawarabiMincho",
        "shipporiMincho",
        "stick",
        "trainOne",
        "Yomogi",
        "yuseiMagic",
        "yujiBoku",
        "yujiMai",
        "yujiSyuku",
        "zenKakuGothicNew",
        "zenMaruGothic",
        "zenOldMincho",
      ],
      answer: "",
      result: null,
      isLoading: true,
    };
  },
  methods: {
    randomTargetFont() {
      const targetNum = Math.floor(Math.random() * 29);
      this.answer = this.men[targetNum];
    },
    checkingAnswers(fontName) {
      switch (fontName) {
        case this.answer:
          // 正解
          this.result = true;
          window.setTimeout(function () {
            location.reload();
          }, 2000);
          break;
        default:
          // 不正解
          this.result = false;
          break;
      }
    },
    close() {
      this.result = null;
    },
    async loading() {
      setTimeout(() => {
        this.isLoading = false;
      }, 2000);
    },
  },
  created() {
    this.randomTargetFont();
  },
  async mounted() {
    await this.loading();
  },
};
</script>

